<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{ products.length }} товара </span>
    </div>

    <section class="cart loading" v-if="cartLoading">
      <BasePreloader />
    </section>
    <section class="cart loading" v-else-if="cartLoadingFailed">
      Не удалось загрузить товары в корзине
    </section>

    <section class="cart" v-else>
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem
              :item="item"
              v-for="item in products"
              :key="item.productId"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | NumberFormat }} ₽</span>
          </p>

          <router-link
            :to="{ name: 'order' }"
            v-if="products.length"
            class="cart__button button button--primery"
            type="submit"
          >
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import NumberFormat from '@/helpers/numberFormat';
import { mapGetters, mapMutations } from 'vuex';
import CartItem from '@/components/CartItem.vue';
import BasePreloader from '@/components/BasePreloader.vue';

export default {
  components: { CartItem, BasePreloader },
  filters: { NumberFormat },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
    }),
    cartLoading() {
      return this.$store.state.cartLoading;
    },
    cartLoadingFailed() {
      return this.$store.state.cartLoadingFailed;
    },
  },
  methods: {
    ...mapMutations(['updateCartLoading', 'updateCartLoadingFailed']),
  },
};
</script>
