<template>
  <router-link
    class="header__cart"
    aria-label="Корзина с товарами"
    :to="{ name: 'cart' }"
  >
    <svg width="30" height="21" fill="currentColor">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span
      class="header__count"
      aria-label="Количество товаров"
      v-if="cartLoading"
      >Загрузка</span
    >
    <span
      class="header__count"
      aria-label="Количество товаров"
      v-else-if="cartLoadingFailed"
      >Ошибка</span
    >
    <span class="header__count" aria-label="Количество товаров" v-else>{{
      $store.state.cartProducts.length
    }}</span>
  </router-link>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
  computed: {
    cartLoading() {
      return this.$store.state.cartLoading;
    },
    cartLoadingFailed() {
      return this.$store.state.cartLoadingFailed;
    },
  },
  methods: {
    ...mapMutations(['updateCartLoading', 'updateCartLoadingFailed']),
  },
};
</script>
