<template>
  <li
    class="cart__item product"
    :class="[
      { cart__item_preloader: productDeleteSending || productDeleteError },
    ]"
  >
    <div class="product__pic">
      <img
        :src="item.product.image"
        width="120"
        height="120"
        alt="item.product.title"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <div class="product__counter form__counter">
      <button
        type="button"
        aria-label="Убрать один товар"
        @click.prevent="counterPageCart('minus')"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <label for="cartItemInput">
        <input type="text" v-model.number="amount" name="count" id="cartItemInput" />
      </label>

      <button
        type="button"
        aria-label="Добавить один товар"
        @click.prevent="counterPageCart('plus')"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
    <div class="cart__delete-error" v-show="productDeleteSending">
      Товар удаляется...
    </div>
    <div class="cart__delete-error" v-show="productDeleteError">
      Произошла ошибка
    </div>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  data() {
    return {
      productDeleteError: false,
      productDeleteSending: false,
    };
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions(['deleteProductCart']),

    deleteProduct() {
      this.productDeleteSending = true;

      this.deleteProductCart({ productId: this.item.productId })
        .catch(() => {
          this.productDeleteError = true;
        })
        .then(() => {
          this.productDeleteSending = false;
        });
    },

    counterPageCart(symbol) {
      if (symbol === 'plus') {
        this.amount += 1;
      }
      if (symbol === 'minus' && this.amount !== 0) {
        this.amount -= 1;
      }
    },
  },
};
</script>

<style>
.cart__item {
  position: relative;
}
.cart__delete-error {
  position: absolute;
  top: 50px;
  left: 340px;
}
.cart__item_preloader *:not(:last-child):not(:nth-child(7)) {
  pointer-events: none;
  opacity: 0.1;
}
</style>
