<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="counterPageCart('minus')">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <label for="counterInput">
      <input type="text" v-model.number="currentProductAmount" @input="input" id="counterInput"/>
    </label>

    <button type="button" aria-label="Добавить один товар" @click.prevent="counterPageCart('plus')">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['productAmount'],
  data() {
    return {
      currentProductAmount: this.productAmount,
    };
  },
  methods: {
    counterPageCart(symbol) {
      if (symbol === 'plus') {
        this.currentProductAmount += 1;
      }
      if (symbol === 'minus' && this.currentProductAmount !== 0) {
        this.currentProductAmount -= 1;
      }
      this.$emit('update:productAmount', this.currentProductAmount);
    },
    input() {
      this.$emit('update:productAmount', this.currentProductAmount);
    },
  },
  watch: {
    productAmount(value) {
      this.currentProductAmount = value;
    },
  },
};
</script>
